
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading,
},
data() {
return {
initialization: {},
money: '',
methodType: {
type: 1
},
methodList: [],

isSend: false,
isSelect: false,
isError: -1,
withdrawRemind: [
'输入金额超过余额',
'最低提现金额不能少于100元'
],

account_number: '',
account_name: '',
ver_code: '',
tel: '',
num: 60,
};
},
created() {

this.tel=this.user.tel;
},
mounted(){
this.$nextTick(function() {
this.getUserWithdraw();
});
},
methods: {
countDown () {
console.log(888)
console.log(this.num)
let c = this.num
c--
if ( c < 1) {
this.num=60;
return
}
this.num=c;
setTimeout(()=>{
this.countDown();
},1000)
},
async getCode () {
this.countDown();
console.log(this.tel)
var tel_user = this.Trim(this.tel);
var that=this;

var param={
tel:tel_user,
plat:3,
type:3,//类型1设置支付密码验证码，2找回支付密码验证码，3提现验证码
time:new Date().getTime(),
version:'1.0.1',
device_name:'MAX123'
};

param=JSON.stringify(param);
param=this.$encrypt(param);

param={
param:param
};
const [err, res] = await api.sendTradePassword(param);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
console.log('发送验证码中')
}
},

Trim(str) {
str=String(str);
return str.replace(/(^\s*)|(\s*$)/g, "");
},
showBankList(){
const isTrue = this.isSelect ? false : true;
this.isSelect=isTrue;
if (isTrue && this.methodList.length < 1) {
this.withdrawMethod();
}
},
async withdrawMethod(){
let data={
plat: 3,
token:this.token,
account:this.account
};
const [err, res] = await api.withdrawMethod(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
console.log(res)
this.methodList=res.data.list;
}
}
},
widthdrawAll(){
this.money=this.initialization.rebate_amout;
console.log(this.money)
},
getMoney(){

},
bindNum(){

},
bindName(){

},
bindCode(){

},
async sureWithdraw(){
if(this.methodType.type != 3 && this.account_number.length < 1 && this.account_name.length < 1) {
this.$vux.toast.text('请输入账号或提现姓名');
return false;
} else if( this.methodType.type == 3 && this.ver_code.length < 1 ) {
this.$vux.toast.text('请输入手机验证码');
return false;
}

var sub_member_id='';
sub_member_id=this.$route.query.sub_member_id;

let data={
plat: 3,
token:this.token,
account:this.account,
amount: this.money,
payment_code: this.methodType.type,
account_number: this.account_number,
account_name: this.account_name,
card_id: this.methodType.card_id,
ver_code: this.ver_code,
tel: this.tel,
sub_member_id: sub_member_id
};

const [err, res] = await api.withdrawCash(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.$router.push('/drawSuccess');
}
}

},
selectType(e){

const index = e.target.dataset.index;
console.log(index)
const data = this.methodList[index];
this.methodType=data;
this.showBankList();
},
async getUserWithdraw(){//1微信2支付宝3银行卡
var sub_member_id='';
sub_member_id=this.$route.query.sub_member_id;

let data = {
plat: 3,
token:this.token,
account:this.account,
sub_member_id:sub_member_id
};
const [err, res] = await api.getUserWithdraw(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
console.log('res：',res);
this.initialization=res.data;
this.methodType.type=res.data.type;
this.methodType.icon=res.data.icon;
this.methodType.title=res.data.title;


}
}

},
linkToBankCard(){
this.showBankList();
this.$router.push('./addCard');
}


},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
},

};



